"use client";

import Link from "next/link";
import css from "./AuthNavigation.module.css";
import { useRouter } from "next/router";
import { useAuthStore } from "@/lib/store/authStore";
import { logout } from "@/lib/api/clientApi";
import { useEffect, useState } from "react";

export default function AuthNavigation() {
  const router = useRouter();
  const { isAuthenticated, clearIsAuthenticated } = useAuthStore((state) => ({
    isAuthenticated: state.isAuthenticated,
    clearIsAuthenticated: state.clearIsAuthenticated,
  }));

  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  const handleLogout = async () => {
    try {
      await logout();
      clearIsAuthenticated();
      router.push("/sign-in");
    } catch (err) {
      console.error("Logout failed", err);
    }
  }

  if (!isMounted) return null; // не рендерим на сервере

  return (
    <ul className={css.navList}>
      {isAuthenticated ? (
        <>
          <li className={css.navigationItem}>
            <Link href="/profile" className={css.navigationLink}>Profile</Link>
          </li>
          <li className={css.navigationItem}>
            <button className={css.logoutButton} onClick={handleLogout}>Logout</button>
          </li>
        </>
      ) : (
        <>
          <li className={css.navigationItem}>
            <Link href="/sign-in" className={css.navigationLink}>Login</Link>
          </li>
          <li className={css.navigationItem}>
            <Link href="/sign-up" className={css.navigationLink}>Sign up</Link>
          </li>
        </>
      )}
    </ul>
  );
}
